import { configureStore, createSlice } from '@reduxjs/toolkit';
import { AuthUser, Reservation } from '../../../types';


const userState = { 
  userUid: '', 
  userEmail: '', 
} as AuthUser;

const emptyReservationState = {
  sitterId: 'empty',
  sitterName: 'empty',
  dateOfBooking: 'empty',
  dayNameOfBooking: 'empty',
  startTime: null,
  endTime: null,
} as Reservation;

const userSlice = createSlice({
  name: 'user',
  initialState: userState,
  reducers: {
    login: (state, action) => {
      state = action.payload;
    },
    logout: (state) => {
      state = userState;
    },
  }, 
});

const reservationSlice = createSlice({
   name: 'reservation',
   initialState: emptyReservationState,
   reducers: {
     reservationdata: (state, action) => {
       state = action.payload;
     },
     success: (state) => {
       state = emptyReservationState;
     },
   }, 
});

export const { login, logout } = userSlice.actions;
export const { reservationdata, success } = reservationSlice.actions;


export const store = configureStore({
    reducer: {
        user: userSlice.reducer,
        reservation: reservationSlice.reducer
    },
});
